import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Grupo_usuario_programa } from '../../../modelo/Grupo_usuario_programa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'grupo_usuario_programa-dados-details',   templateUrl: 'grupo_usuario_programa_dadosDetails.component.html' }) export class Grupo_usuario_programaDadosDetailsComponent implements OnInit {    grupo_usuario_programa: Grupo_usuario_programa;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.grupo_usuario_programa.Grupo_usuario_programa";                 let id = +params['id'];         this.grupo_usuario_programa = new Grupo_usuario_programa();         this.grupo_usuario_programa.cd_grupo_usuario_programa = id;         o.objetoJson = this.grupo_usuario_programa;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a grupo_usuario_programa. '; }      this.grupo_usuario_programa = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


